import React, {FormEvent} from "react";
import styles from "../registration/registration.module.css";
import {Button, Input, PasswordInput} from "@ya.praktikum/react-developer-burger-ui-components";
import clsx from "clsx";
import {Link, Location, Navigate, useLocation, useNavigate} from "react-router-dom";
import {fetchResetPass} from "../../services/reducers/auth-slice";
import {TFromLocation, TResetPass, useAppDispatch} from "../../utils/types";
import {useForm} from "../../hooks/use-form";

export default function ResetPassword() {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const location: Location<TFromLocation> = useLocation();

    const {form, onChange} = useForm<TResetPass>({
        password: "",
        code: "",
    });

    if (!location.state || location.state.from !== "/forgot-password") {
        return <Navigate to={'/'}/>
    }

    const onSubmit = (event: FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(fetchResetPass(form));
        navigate('/login');
    }

    return (
        <section className={styles.container}>
            <h1 className="text text_type_main-large"> </h1>
            <form className={clsx(styles.form, "mt-6")} onSubmit={onSubmit}>
                <PasswordInput value={form.password} name="password" placeholder={"  "}
                               onChange={onChange}/>
                <Input value={form.code} onChange={onChange} name="code" placeholder={"   "}/>
                <Button htmlType={"submit"} size="medium"></Button>
            </form>
            <div className={clsx(styles.linkBlock, "mt-20")}>
                <p className={"text text_type_main-default"}> ?</p>
                <Link to="/login"
                      className={clsx(styles.link, "text text_type_main-default")}></Link>
            </div>
        </section>
    )
}